
h1{
  text-align:center;
  color:#f00;
  font-size:20px;
}
h2{
  font-size:18px;
  background:#ff0;color:#00f;padding:5px;
}
h3{
  font-size:17px;
  background:#ccc;padding:4px;
}
p{
  font-size:16px;
}
.login_form{
  text-align: center;
  border: none;
  margin-top:50px;
  position: relative;
}
legend{
  font-size:20px;
  font-weight: bold;
  margin:10px auto;
}
label{
  display:none;
}
input{
  width:250px;
  height:30px;
}
.find_box{
  position: absolute;
  top:100px;
  right:100px;
}
.find_box a{
  color:#000;
  text-decoration: none;
  font-size:13px;
}
.login_btn{
  background-color:purple;
  color:#fff;
  border: none;
  margin:15px 0px;
  height:40px;
}
.join_btn{
  background-color: #fff;
  color:purple;
  border: 1px solid purple;
  height:40px;
}
.see_box{
  font-size:13px;
  position: absolute;
  right:100px;
}